import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

const ShapeSelection = ({id, go}) => (
	<Panel id={id} style={{overflow: 'hidden'}}>
		<PanelHeader>
			ВЫБЕРИТЕ <strong>ФОРМУ</strong>
		</PanelHeader>
		<Group className="Group">

			<FixedLayout vertical="bottom">
				<Div style={{display: 'flex'}}>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onLeft} alt="onLeft"/>}
						stretched 
						level="2" 
						onClick={go} 
						data-to="viewSelection"
					></Button>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onRight} alt="onRight"/>}
						stretched 
						level="2" 
						onClick={go} 
						data-to="sizeSelection"
					></Button>
				</Div>
			</FixedLayout>
		</Group>
	</Panel>
);


ShapeSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default ShapeSelection;
